<template>
<div class="h5-file-up" v-if="init.key">
    <div class="box">
        <div class="content-header">
            <h4>图片列表</h4>
            <i @click="init.key=false"></i>
        </div>
        <div class="content" ref="h5_content">
            <div class="image-list">
                <input type="file" ref="file" style="display: none;" id="img-upload" multiple="multiple" accept="image/*"  @change="uploadImg($event)"/>
                <ul class="list-ul">
                    <li class="list-li" v-for="(img,index) in imgArr" :key="index">
                        <div class="list-link">
                            <i class="link-img" :style="{backgroundImage:'url('+img.src+')'}"  @click.stop='clipLoad($event,img.src,index)'></i>
                            <b>点图裁剪</b>
                        </div>
                        <span class="list-img-close" @click.stop='delImage(index)'></span>
                        <span class="list-img-rotate" @click.stop="rofn($event,img.src,index)"></span>
                    </li>
                    <li class="list-li-add">
                        <span class="add-img" @click.stop="addPic"></span>
                    </li>
                </ul>
            </div>
        </div>
        <div class="content-footer">
            <button @click="saveImg">保存</button>
        </div>
    </div>
    <div class="ab" ref="alertBox">
        <div class="alert-box">
            <div class="ab-header">
                <h4>图片裁剪</h4>
                <i @click="closeab"></i>
            </div>
            <div class="ab-content" ref="ab_content">
                <div class="htmleaf-container" ref="hc_content">
                    <div id="clipArea" ref="clip"></div>
                    <div id="view" ref="view"></div>
                </div>
            </div>
            <div class="ab-footer">
                <div id="dpage">
                    <button id="clipBtn">截取图片</button>
                </div>
            </div>
        </div>
    </div>
</div>
</template>
<script>
    //import EXIF from 'exif-js'
    import Exotic from '../../../config.js'
    //import VConsole from 'vconsole'
    import './jquery.photoClip.js'
    export default{
        name:"image-html5-upload",
        data(){
            return {
                imgArr:[],
                cl:0,
                imgjia:require('./icon-jia.png')
            }
            
        },
        created(){
            //var vConsole = new VConsole();
            this.clipBox=false
        },
        props:['imgNumLimit','init','parentImgArr'],
        methods:{
            rt(){
                
            },
            saveImg(){
                var that=this
                if(that.imgArr.length==0){
                    //that.MintUi.MessageBox('提示', '请选择图片');
                    Exotic.tip({txt:"请选择图片"})
                }else{
                    that.fileArr=[]
                    for(let i=0;i<that.imgArr.length;i++){
                        var file=encodeURIComponent(that.imgArr[i].src)
                        that.fileArr.push(file)
                    } 
                    console.log(that.fileArr)
                    that.MintUi.Indicator.open({
                        text: '上传中...',
                        spinnerType: 'fading-circle'
                    });
                    (function bl(i){
                        i++;
                        if(i==that.fileArr.length){
                            console.log(i)
                            that.imgArr=[];
                            that.init.key=false;
                            that.MintUi.Indicator.close();
                            return;
                        }else{
                            var timer=null;
                            Exotic.uploadFileData(that.fileArr[i],function(res){
                                let url=Exotic.imageUrl+res.url;
                                clearTimeout(timer)
                                timer=setTimeout(function(){
                                    if(that.imgNumLimit==1){
                                        that.parentImgArr.splice(0,1)
                                         that.parentImgArr.push({
                                            id:new Date().getTime().toString()+parseInt(Math.random()*90000+10000),
                                            alt:'',
                                            url:url,
                                            dataurl:that.imgArr[i].src
                                        });
                                        that.$emit('imgIndex',{url:url,index:that.imgArr.length-1});
                                        bl(i)
                                    }else{
                                        that.parentImgArr.push({
                                            id:new Date().getTime().toString()+parseInt(Math.random()*90000+10000),
                                            alt:'',
                                            url:url,
                                            dataurl:that.imgArr[i].src
                                        });
                                        bl(i)
                                    }
                                },40)
                            })
                        }
                    }(-1))
                }
                //console.log(this.imgArr[0].src)
            },
            closeab(){
                var _this=this
                this.clipBox=false
                _this.$refs.alertBox.style.display="none"
            },
            dataURLtoFile(dataurl, filename) {//将base64转换为文件
                var arr = dataurl.split(','), mime = arr[0].match(/:(.*?);/)[1],
                bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
                while(n--){
                    u8arr[n] = bstr.charCodeAt(n);
                }
                return new File([u8arr], filename, {type:mime});
            },
            clipLoad(ev,src,index){
                var _this=this
                this.clipBox=true
                _this.$refs.alertBox.style.display="block"
                $("#clipArea").photoClip({
                    width: 280,
                    height: 280,
                    file: src,
                    type:'base64',
                    view: "#view",
                    ok: "#clipBtn",
                    loadStart: function() {
                        console.log("照片读取中");
                    },
                    loadComplete: function() {
                        console.log("照片读取完成");
                    },
                    clipFinish: function(dataURL) {
                        _this.imgArr[index].src=dataURL
                        this.clipBox=false
                        _this.$refs.alertBox.style.display="none"
                    }
                });
            },
            delImage(index){
                let _this = this;
                _this.MintUi.MessageBox.confirm('确定删除该图片?').then(function(action){
                    if(_this.imgNumLimit==1){
                         for(let i=0;i<_this.imgArr.length;i++){
                             _this.imgArr.splice(i,1)
                         }
                    }else{
                         _this.imgArr.splice(index, 1);
                    }
                    _this.$refs.file.value=""
                },function(err){
                    console.log(err);
                });
            },
            addPic(e) {
                let _this = this;
                // $('input[type=file]').trigger('click');
                this.$refs.file.click();
            },
            convertBase64UrlToBlob(urlData){  
                
                var bytes=window.atob(urlData.split(',')[1]);        //去掉url的头，并转换为byte  
                
                //处理异常,将ascii码小于0的转换为大于0  
                var ab = new ArrayBuffer(bytes.length);  
                var ia = new Uint8Array(ab);  
                for (var i = 0; i < bytes.length; i++) {  
                    ia[i] = bytes.charCodeAt(i);  
                }  
            
                return new Blob( [ab] , {type : 'image/png'});  
            },
            rofn(ev,src,index){
               
                var _this=this
                console.log(index)
                console.log(_this.imgArr)

                var file=_this.convertBase64UrlToBlob(src)
                let reader = new FileReader();
                reader.readAsDataURL(file);
                reader.onload = function(){
                    var oReader = new FileReader();
                    oReader.onload = function(e) {
                        var image = new Image();
                        image.src = e.target.result;
                        image.onload = function() {
                            var expectWidth = this.naturalWidth;
                            var expectHeight = this.naturalHeight;
                            if (this.naturalWidth > this.naturalHeight && this.naturalWidth > 800) {
                                expectWidth = 800;
                                expectHeight = expectWidth * this.naturalHeight / this.naturalWidth;
                            } else if (this.naturalHeight > this.naturalWidth && this.naturalHeight > 1200) {
                                expectHeight = 1200;
                                expectWidth = expectHeight * this.naturalWidth / this.naturalHeight;
                            }
                            var canvas = document.createElement("canvas");
                            var ctx = canvas.getContext("2d");
                            canvas.width = expectWidth;
                            canvas.height = expectHeight;
                            ctx.drawImage(this, 0, 0, expectWidth, expectHeight);
                            var base64 = null;
                            _this.rotateImg(this,'left',canvas);
                            base64 = canvas.toDataURL("image/jpeg", 0.8);
                            if(file.size / 1024000 > 1){
                                _this.imgScale1(base64, 4,function(src){
                                    _this.imgArr[index].src=src
                                })
                            }else{
                                _this.imgArr[index].src=base64
                            }
                            
                        }
                    }
                    oReader.readAsDataURL(file);
                }
            },
            "uploadImg": function(e){
                let tag = e.target;
                let fileList = tag.files;
                console.log(tag)
                console.log('改变图片：')
                console.log(fileList)
                let imgNum = fileList.length;
                let _this = this;
                // this.cl++;
                let imgLength=this.imgArr.length
                // _this.imgArr = [];//图片数据清零
                if(this.imgNumLimit>1){
                    imgLength=_this.parentImgArr.length+imgLength
                }
                if( imgLength+ imgNum > this.imgNumLimit){
                    if(this.imgNumLimit==1){
                        Exotic.tip({txt:"只能上传1张图片"})
                    }else{
                        Exotic.tip({txt:"最多只能上传"+(this.imgNumLimit-_this.parentImgArr.length)+"张图片"})
                    }
                    return;
                }
                for(let i=0;i<imgNum;i++){
                    let reader = new FileReader();
                    reader.readAsDataURL(fileList[i]);
                    reader.onload = function(){
                        var oReader = new FileReader();
                        oReader.onload = function(e) {
                            var image = new Image();
                            image.src = e.target.result;
                            image.onload = function() {
                                var expectWidth = this.naturalWidth;
                                var expectHeight = this.naturalHeight;
                                if (this.naturalWidth > this.naturalHeight && this.naturalWidth > 800) {
                                    expectWidth = 800;
                                    expectHeight = expectWidth * this.naturalHeight / this.naturalWidth;
                                } else if (this.naturalHeight > this.naturalWidth && this.naturalHeight > 1200) {
                                    expectHeight = 1200;
                                    expectWidth = expectHeight * this.naturalWidth / this.naturalHeight;
                                }
                                var canvas = document.createElement("canvas");
                                var ctx = canvas.getContext("2d");
                                canvas.width = expectWidth;
                                canvas.height = expectHeight;
                                ctx.drawImage(this, 0, 0, expectWidth, expectHeight);
                                var base64 = null;
                                base64 = canvas.toDataURL("image/jpeg", 0.8);  
                                console.log(fileList[i])
                                if(fileList[i]){
                                    if(fileList[i].size / 1024000 > 1){
                                        _this.imgScale(base64, 0.5)
                                    }else{
                                        _this.imgArr.push({"src": base64});
                                    }
                                }
                                console.log('选择图片');
                                if(i>=(imgNum.length-1)){
                                    _this.$refs.file.value=""
                                }
                            };
                        };
                        oReader.readAsDataURL(fileList[i]);
                    }
                }
            },
            "imgScale": function(imgUrl,quality){
                let img = new Image();
                let _this = this;
                let canvas = document.createElement('canvas');
                let cxt = canvas.getContext('2d');
                img.src = imgUrl;
                img.onload = function(){
                    //缩放后图片的宽高
                    let width = img.naturalWidth/quality;
                    let height = img.naturalHeight/quality;
                    canvas.width = width;
                    canvas.height = height;
                    cxt.drawImage(this, 0, 0, width, height);
                    _this.imgArr.push({"src": canvas.toDataURL('image/jpeg')});
                     console.log('压缩后：');
                     console.log(_this.imgArr);
                }
            },
            "imgScale1": function(imgUrl,quality,cb){
                let img = new Image();
                let _this = this;
                let canvas = document.createElement('canvas');
                let cxt = canvas.getContext('2d');
                img.src = imgUrl;
                img.onload = function(){
                    //缩放后图片的宽高
                    let width = img.naturalWidth/quality;
                    let height = img.naturalHeight/quality;
                    canvas.width = width;
                    canvas.height = height;
                    cxt.drawImage(this, 0, 0, width, height);
                    cb&&cb(canvas.toDataURL('image/jpeg'))
                }
            },
            "rotateImg":function (img, direction,canvas) {//图片旋转
                var min_step = 0;
                var max_step = 3;
                if (img == null)return;
                var height = img.height;
                var width = img.width;
                var step = 2;
                if (step == null) {
                    step = min_step;
                }
                if (direction == 'right') {
                    step++;
                    step > max_step && (step = min_step);
                } else {
                    step--;
                    step < min_step && (step = max_step);
                }
                var degree = step * 90 * Math.PI / 180;
                var ctx = canvas.getContext('2d');
                switch (step) {
                    case 0:
                        canvas.width = width;
                        canvas.height = height;
                        ctx.drawImage(img, 0, 0);
                        break;
                    case 1:
                        canvas.width = height;
                        canvas.height = width;
                        ctx.rotate(degree);
                        ctx.drawImage(img, 0, -height);
                        break;
                    case 2:
                        canvas.width = width;
                        canvas.height = height;
                        ctx.rotate(degree);
                        ctx.drawImage(img, -width, -height);
                        break;
                    case 3:
                        canvas.width = height;
                        canvas.height = width;
                        ctx.rotate(degree);
                        ctx.drawImage(img, -width, 0);
                        break;
                }
            },
            _calcHeight:function(){
                var that=this
                var wh=document.documentElement.clientHeight;
                var heg = wh-92;
                try{
                    if (that.$refs.h5_content){
                        that.$refs.h5_content.style.height = heg + 'px'
                        that.$refs.ab_content.style.height = heg + 'px'
                        that.$refs.hc_content.style.height = heg + 'px'
                        that.$refs.clip.style.height = heg + 'px'
                        that.$refs.view.style.height = heg + 'px'
                    }
                }catch(err){

                }
                window.addEventListener('resize',function(){
                    var wh = document.documentElement.clientHeight;
                    var heg = wh - 92;
                    try{
                        if (that.$refs.h5_content){
                            that.$refs.h5_content.style.height = heg + 'px'
                            that.$refs.ab_content.style.height = heg + 'px'
                            that.$refs.hc_content.style.height = heg + 'px'
                            that.$refs.clip.style.height = heg + 'px'
                            that.$refs.view.style.height = heg + 'px'
                        }
                    }catch(err){

                    }
                })
            }
        },
        mounted(){
            this._calcHeight();
        }
    }
</script>
<style scoped>
.h5-file-up{
    height:100%;
    width:100%;
    position:fixed;
    z-index: 2001;
    top:0;
    left:0;
    background-color:#fff;
}
.ab{
    display: none;
}
.h5-file-up .box{
    display: block;
    width: 100%;
    position: static;
    height:100%;
    top: 0;
    margin-top: 0;
    left: 0;
    border-radius: 0;
}
.h5-file-up .content-header{
    padding:0;
    margin:0;
    background-color: #eee;
    text-indent: 10px;
    font-size:14px;
    font-weight:normal;
    text-align: center;
}
.h5-file-up .list-default-img{
    width: 90px;
    height: 90px;
    overflow: hidden;
    display: block;
    padding: 0;
    position: relative;
}
.h5-file-up .list-default-img img{
    display: block;
    width: 90px;
    height: 90px;
}
.h5-file-up .add-img{
    width:90px;
    height:90px;
    display: block;
    background-image: url(./icon-jia.png);
    background-position: center;
    background-size:100%;
    background-repeat: no-repeat;
}
.h5-file-up .list-ul li{
    float:left;
    width:90px;
    margin-left:15px;
    margin-top:15px;
    position: relative;
    margin-right:10px;
}
.h5-file-up .list-img-close{
    width:26px;
    height:26px;
    display: block;
    position: absolute;
    right: -10px;
    top: -5px;
    background-image: url(./icon-cha.png);
    background-position: center;
    background-size:100%;
    background-repeat: no-repeat;
}
.h5-file-up .list-link{
    display: block;
    position: relative;
}
.h5-file-up .list-ul .list-li-add{
    display: block;
    width: 90px;
    height: 90px;
    clear:left;
    margin-top:15px;
}
.h5-file-up .list-link b{
    font-size:12px;
    font-weight:normal;
    position:absolute;
    bottom:0;
    left:0;
    width:100%;
    display: block;
    text-align: center;
    height:16px;
    line-height:16px;
    color:#fff;
    background-color:rgba(0,0,0,.5);
}
.h5-file-up .list-link .link-img{
    display: block;
    width:90px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 90px;
    background-color: #000;
    height: 90px;
}
.h5-file-up .list-img-rotate{
    width: 26px;
    height: 26px;
    display: block;
    position: absolute;
    right: -10px;
    bottom: -4px;
    background-color:#fff;
    border-radius:50%;
    background-image: url(./icon-rotate.svg);
    background-position: center;
    background-size: 100%;
    background-repeat: no-repeat;
}
.h5-file-up .alert-box{
    width:100%;
    position: fixed;
    background-color:#fff;
    height:100%;
    top:0;
    left:0;
}
.h5-file-up .ab-header,
.h5-file-up .content-header{
    height:40px;
    line-height:40px;
    font-size:14px;
    text-align: center;
}
.h5-file-up .ab-content,
.h5-file-up .content{
    min-height: 300px;
}
.h5-file-up .ab-header h4,
.h5-file-up .content-header h4{
    margin:0;
    padding:0;
    text-decoration: none;
    font-weight: normal;
    text-indent: 1rem;
    background-color: #eee;
}
.h5-file-up .ab-footer,
.h5-file-up .content-footer{
    height:42px;
    line-height:42px;
    padding: 5px 10px;
}
.h5-file-up .ab-footer button,
.h5-file-up .content-footer button{
    width: 100%;
    padding: 10px 0;
    background-color: #e56fa6;
    color: #fff;
    border-radius: 5px;
    outline: none;
    font-size: 14px;
    border: 0;
}
.h5-file-up .htmleaf-container {
    margin: 0 auto;
    text-align: center;
    overflow: hidden;
    width: 100%;
    height: 100%;
    background: #383838;
}
#clipArea{
    height:100%;
}
.h5-file-up .ab-header,
.h5-file-up .content-header{
    position: relative;
}
.h5-file-up .ab-header i,
.h5-file-up .content-header i{
    width: 20px;
    height: 20px;
    background: url(./icon-cha.png) no-repeat center;
    background-size: 18px;
    display: block;
    position: absolute;
    top: 10px;
    right: 10px;
}
</style>



